import React, { Component, Fragment } from "react";
import styled from "styled-components";

const WomanWrapper = styled.div`
  width: 100vw;
  padding: 100px;
  outline: 1px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  h1 {
    color: white;
  }
`;

const calcX = x => {
  if (typeof window === "undefined") {
    return 0;
  } else {
    // measure centerpoint
    const winWidth = window.innerWidth;
    const center = winWidth / 2;
    const distFromCenter = x - center;
    const decimal = distFromCenter / center;
    console.log({ x, winWidth, center, distFromCenter, decimal });
  }
};

export const WomenList = ({ women, x, y }) => {
  console.log(this.calcX(this.state.x));
  if (women) {
    const allWomen = women.map(woman => {
      return (
        <WomanWrapper key={woman.id}>
          <h1>{woman.name}</h1>
        </WomanWrapper>
      );
    });
    return allWomen;
  } else return null;
};
